    padding: 40px 0;
}
.join-image-wrapper{
    margin: 40px 0;
    text-align: center;
    img{
        display: inline-block;
    }
}
.join-content-wrapper{
    margin: 40px 0 40px 4px;
    h2{
        font-family: 'GT Super Txt Trial';
