.bannar-wrapper{
    background: rgba(242, 224, 133, 1);
    position: relative;
.bannar-left-image{
    img{
        width: 100%;
    }
}

.bannar-right-wrapper{
 margin-top: 80px;
 margin-bottom: 80px;
    .bannar-wev-image{
        margin-bottom: 70px;
        img{

        }
    }
    h1{
        font-family: 'GT Super Txt Trial';
font-style: normal;
font-weight: 700;
font-size: 72px;
line-height: 83px;
text-align: center;
color: #000000;
    }
    p{
      margin-top: 32px;
        font-family: 'Jost';
font-style: normal;
font-weight: 400;
font-size: 20px;
line-height: 30px;

text-align: center;


color: #000000;
    }
    .bannar-btn-area {
        position: relative;
        z-index: 1;
        display: inline-block;
        margin-top: 64px;
        a.btn {
            font-family: 'GT Super Txt Trial';
            font-style: normal;
            font-weight: 700;
            font-size: 24px;
            display: inline-block;
            background-color: rgba(181, 179, 92, 1);
            padding: 14px 40px 14px;
            border-radius: 0;
            border: 2px solid #000000;
            color: #000000 !important;
        }
    }
    .bannar-btn-area::after {
        position: absolute;
        content: "";
        top: 6px;
        right: -6px;
        width: 100%;
        height: 100%;
        border: 2px solid #000000;
        z-index: -1;
        opacity: 0;
        transition: .5s;
    }
    .bannar-btn-area:hover.bannar-btn-area::after {
        opacity: 1;
    }
}

.bannar-shape-1{
   
        animation: 3s ease-in-out .6s infinite upAndDown;
        
      
    img{
        text-align: center;
        @include breakpoint(xsmall){
            width: 120px;
        }
        @include breakpoint(small){
            width: 120px;
        }
        @include breakpoint(large){
            width: auto;
        }
    }
@include breakpoint(xsmall){
    position: absolute;
    top: 30.5%;
    right: 0px;
    z-index: 1;
  
    text-align: center;
    
}
@include breakpoint(small){ 

    position: absolute;
    top: 39.5%;
    right: 28px;
    z-index: 1;
  
    text-align: center;


 }
@include breakpoint(medium){ 

    position: absolute;
    top: 56%;
    right: 32px;
    z-index: 1;
    text-align: center;


 }
@include breakpoint(xlarge){
    
    position: absolute;
    left: 0;
    right: 0;

    bottom: 115px;
    z-index: 1;
    text-align: center;
    
}
}
.bannar-shape-2{
    position: absolute;
    animation:  60s ease-in-out .6s infinite rotate-center;
    
    z-index: 10;
    text-align: center;
    @include breakpoint(xsmall){  right: 39px;bottom: -50px; }

    @include breakpoint(large){  right: 69px; bottom: -90px; }
    img{
        text-align: center;
        @include breakpoint(xsmall){
            width: 120px;
        }
        @include breakpoint(large){
            width: auto;
        }
    }
}



}



  
  @keyframes upAndDown {
    0%,
    100% {
      transform: translate3d(0, 0, 0);
    }
    50% {
      transform: translate3d(0, 5%, 0);
    }
  }

  @keyframes rotate-center {
    0%{
      transform: rotate(0);
    }
    100%{
      transform: rotate(360deg);
    }
  }