.main-header-wrapper {
.submenu{
  opacity: 0;
  visibility: hidden;
  padding:0;
  text-align: center;
   width: 100%;
  position: absolute;
  top: 100%;


  background: #fff;
  transition: 0.3s all ease-in-out;
 
}

.singel-menu{
  padding: 10px 0;
  
  ul{
    li{
      text-align: left;
      display: block;
      
      a{
        font-family: 'GT Super Txt Trial';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        margin: 24px;
        padding: 0;
        color: #000000;
      }
    }
  }
  
}
}

.main-menu ul li:hover > .submenu{
 opacity: 1;
 visibility: visible;
 top: 101%; 
 z-index: 1000;
}

