.footer-wrapper{
    background-color: $current-Olive;
    padding: 60px 0;

    .footer-logo{
        margin: 50px 0 24px 0;
        text-align: center;
        img{
            display: inline-block;
        }
    }
    .footer-socail{
        text-align:center;
        ul{
            li{
                margin: 0 19px;
            }
        }
    }
}

.footer-right-menu{
    margin: 50px 0 42px 0;
    .sinel-menu-item{
        
        h6{
            font-family: 'GT Super Txt Trial';
            font-style: normal;
            font-weight: 700;
            font-size: 14px;
            line-height: 30px;
            color: #F8F8F8;
            margin-bottom: 40px;


            @include breakpoint(small){
                font-size: 16px;
            }
            @include breakpoint(medium){
                font-size: 20px;
            }
        }
    }
    .singel-menu-item{
        margin: 40px 0px;
        li{
            display: block;
            a{
                font-family: 'Jost';
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                display: block;
                line-height: 30px;
                color: #F8F8F8;
                margin: 16.5px 0;
                @include breakpoint(small){
                    font-size: 16px;
                }
                @include breakpoint(medium){
                    font-size: 20px;
                }
        }}
    }
}


.footer-copy-right{
    background-color: $current-Olive;
    padding: 30px;
    border-top: $border-top;
    border-bottom: $border-bottom;
    border-color: $current-Mild-Olive;
.f-copy-text{
    p{
        font-family: 'Jost';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 1;
        line-height: 24px;
        color: #F8F8F8;
    }
}}


.footer-wrapper{
    position: relative;
    .footer-human-shap{
        position: absolute;
  
        
        @include breakpoint(xsmall){
            top: -75px;
            left: 04px;
   
        }
        @include breakpoint(small){
            top: -75px;
            left: 74px;
        }
        
        @include breakpoint(medium){
            top: -75px;
            left: 74px;
        }
        @include breakpoint(large){
            top: -96px;
            left: 20px;
        }
        @include breakpoint(xlarge){
            top: -121px;
            left: 20px;
        }
        @include breakpoint(xxlarge){
            top: -154px;
        left: 200px;
        }
        img{
            @include breakpoint(small){
   
            }
            @include breakpoint(xsmall){
                width: 150px;
            }
            
            @include breakpoint(medium){
                width: 150px;
            }
            @include breakpoint(large){
                width: 200px;
            }
            @include breakpoint(xlarge){
              width: auto;
            }
        }
        .footer-human-shap.proudct-area-shap{
            top: -134%  ;
        }
    }
}




.sinel-menu-item {
   
    @include breakpoint(xsmall){
        margin-left: 17px;


    }
    @include breakpoint(small){
        margin-left: 0px;

    }
}