// This is the common css here everthing same about all sections 

.section-title {
    h2{


font-family: 'GT Super Txt Trial';
font-style: normal;
font-weight: 700;
font-size: 30px;
line-height: 60px;
margin: 28px 0;
@include breakpoint(small){
    font-size: 40px;
}
color: #000000;
    }
}

.common-margin-common {
    overflow: hidden;
    @include breakpoint(xxlarge){
        margin: 10px 45px 0 45px;
    }

}

.slider-wrapper{
    margin: 28px 0;
}


.common-product-wrapper{


    padding: 60px 0px;
    border-top: 1px solid #000000;
    @include breakpoint(xxlarge){
        padding: 60px 0px;
        border-top: 1px solid #000000;
    }
.singel-product-svg-wrapper{
    text-align: center;
    margin: 28px 0;
    .svg-icon-here{
        display: inline-block;
    }
}


.custome-arrow{
    width: 40px;
    height: 40px;
    background: #fff;
    border: 2px solid #000000;
    border-radius: 0;
    opacity: 1;
    margin: 0 12px;

}



button.splide__arrow.custome-arrow.active {
    background: #B5B35C;
}
}


.splide__arrows{
    padding: 25px 0 40px 0;
}

.svg-icon-here{
    position: relative;
    .prudct-shado-svg{
        position: absolute;
        z-index: -1;
        top: 12px;
        left: 12px;
        opacity: 0;
        transition: 0.4s;
        
    }
    &:hover .prudct-shado-svg{
        opacity: 1;
    }
}



// common  details css here
.prouct-details{
    h2{
        font-family: 'GT Super Txt Trial';
        font-style: normal;
        font-weight: 700;
       
        line-height: 36px;
        font-size: 22px;
        line-height: 53px;
        text-align: center;
        margin: 25px 0 4px 0;
        @include breakpoint(small){
            font-size: 24px;
       
        }

    }
    p{
        margin: 8px 0 45px 0;
        font-family: 'Jost';
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 30px;
    }
}
.ptype-1.prouct-details{
    h2{
        color: #000000;
       padding: 0 9px;
    }
    p{
        color: #B5B35C;

    }
}
.egg-mobile-respinsive{
    width:260px;
    @include breakpoint(small){
        width:309px;
        height: 383px;
        
    }
}
.common-product-wrapper.t-shirt{
 margin: 0;
    padding: 60px 0px;
    @include breakpoint(xxlarge){
         margin: 0;
        padding: 60px 37px;
    }
}
.extra-b-margin{

@include breakpoint(xlarge){
    padding: 0 20px;
}
@include breakpoint(xxlarge){
    padding: 0px;
}
    
}
.t-shirt{
    background: #5E6B61;
    .section-title{
        h2{
            color: #fff;
        }
    }
}
.t-shirt-content{
    h2{
        color: #fff;
    }
    p{
        color: #B5B35C;
    }
}

.t-shirt-mobile {
    
    width:280px;
    height: auto;
    @include breakpoint(small){
        width:424px;
        height:593px;
    }
}





