.newslatter-wrapper{
    padding: 104px 0;
 background: #F2E085;
 position: relative;


 .section-title{
    h2{
        font-family: 'GT Super Txt Trial';
font-style: normal;
font-weight: 700;



@include breakpoint(xsmall){
    font-size: 30px;
}
@include breakpoint(small){
    font-size: 56px;
    line-height: 84px;
}
@include breakpoint(xlarge){
    font-size: 40px;
    line-height: 84px;
}
@include breakpoint(xxlarge){
    font-size: 56px;
    line-height: 84px;
}
    }
 }
 .newslater-shape{
    animation: 3s ease-in-out .6s infinite upAndDown;
    position: absolute;
    right: 86px;
    bottom: 52px;
    @include breakpoint(xsmall){
        right: 60px;
        top: 10px;
    }
    @include breakpoint(small){
        right: 60px;
        top: 3px;
    }
    @include breakpoint(medium){
        right: 86px;
        top: 20px;
    }
    @include breakpoint(large){
        right: 86px;
        bottom: 52px;
    }
    img{
        @include breakpoint(xsmall){
           width: 100px;
        }
        @include breakpoint(small){
            width: auto;
        }
    }
 }
.form-wrapper{
    text-align: center;
    .form-group{
        display: inline-block;
       
        border: 2px solid #000000;
        border-radius: 4px;
        @include breakpoint(xsmall){
            width:100%;
            height: 45px;
        }
        @include breakpoint(small){
            width: 424px;
        height: 64px;
        }
        input.form-control {
            padding-left: 32px;
        }
        input.form-control::placeholder{
            font-family: 'Jost';
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 30px;
            
        }
    }
}
button.form-btn {

    border: none;
    border-left: 3px solid #000000;
    background: #B5B35C;
    @include breakpoint(xsmall){
        height: 41px;
    width: 60px;
    }
    @include breakpoint(small){
        height: 60px;
    width: 60px;
    }
}

}

