.splide__pagination {
    bottom: -2.5em;
    left: 0;
    padding: 0 1em;
    position: absolute;
    right: 0;
    z-index: 1;
}

ul.splide__pagination.splide__pagination--ltr{
    li{
        .splide__pagination__page{
            width: 10px;
            height: 10px;
            margin: 5px;
            padding: 5px;
            -webkit-transform: scale(1.4);
            -ms-transform: scale(1.4);
            transform: scale(1.4);
            background: #000000;
            opacity: 0.4;
        }
        .splide__pagination__page.is-active{
           opacity: 1;
           border: 1px solid #000;
          position: relative;
        
        }
        .splide__pagination__page.is-active::before{
            position: absolute;
            content: '';
            width: 8px;
            height: 8px;
            background: transparent;
            border: 2px solid #ddd;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            border-radius: 50px;
        }
    }
}

.splide__pagination li {
    display: inline-block;
    line-height: 0;
    list-style-type: none;
    margin: 0;
    padding: 0;
    pointer-events: auto;
}





